export function getProjects() {
  return projects;
}

export function getProject(url) {
  return projects.find(p => p.url === url);
}

export function getPrevNextProject(url, isNext = true) {
  console.log(url, isNext);
  if (!url) return null;

  let index = projects.findIndex(p => p.url === url);

  if (isNext === false && index === 0) index = projects.length - 1;
  else if (isNext === true && index === projects.length - 1) index = 0;
  else index += isNext ? 1 : -1;
  console.log(index, projects.length, projects[index]);

  return projects[index].url;
}
